import { handleActions } from "redux-actions";
import {addUpdatedMessage, deleteUpdatedMessage, getChatsSuccess, saveMessage} from "../actions/chats";

const initialStore = {
    byIds: {},
    ids: [],
};

const reducer = handleActions(
    {
        [getChatsSuccess] : (store, { payload }) => ({
            ...store,
            byIds: payload.reduce((sum, item) => {
                sum[item.id] = item;
                return sum;
            }, {}),
            ids: payload.map(({id}) => id),
        }),
        // [deleteChat]: (store, { payload }) => ({
        //     ...store,
        //     ids: store.ids.filter(id => id !== payload),
        // }),
        [saveMessage]: (store, { payload }) => ({
            ...store,
            byIds: {
                ...store.byIds,
                [payload.chatId]: {
                    ...store.byIds[payload.chatId],
                    messageList: [...store.byIds[payload.chatId].messageList, payload.message],
                }
            }
        }),
    },
    initialStore,
);

export default reducer;
